.App.dark {
    background-color: #111;
    color: rgb(156, 156, 156);
    .homecontainer{
        background-color: #111;

    }

    .singleContainer {
      background-color: #111;
    }

    .newContainer {
      background-color: #111;
    }
    .chartGrid {
      stroke: rgba(228, 228, 228, 0.219);
    }
  
    .table {
      background-color: #111;
  
      .tableCell {
        color: gray;
      }
    }
    
    .navbar {
      background-color: #111;
      color: #999;
      border-color: #333;
  
      .search {
        border-color: gray;
      }
    }
    
    .sidebar {
      background-color: #111;
      border-color: #333;
  
      .top {
        .logo {
          color: #999;
        }
      }
      hr {
        border-color: #333;
      }
  
      ul {
        li {
          &:hover {
            background-color: #333;
          }
  
          .icon {
            color: #999;
          }
        }
      }
    }
  
    .listContainer {
      background-color: #111;
      .datatable {
        background-color: #111;
        
        
        .MuiTablePagination-root{
          color: gray;
        }
  
        .datagrid {
          color: gray;
          border: none;
          background-color: #111;
  
    
          .viewButton,
          .deleteButton,
          .cellWithStatus {
            color: gray;
            border: none;
          }
        }
      }
    }
  
    input{
        background-color: transparent;
    }
  }